
import Loading from 'vux/src/components/loading/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import InfiniteLoading from 'vue-infinite-loading'
import api from '@/utils/api'
import { formatLink, getLimitTitle } from '@/utils/utils'
import MySearch from '@/components/MySearch'

export default {
name: 'QsList',
components: {
Loading,
InfiniteLoading,
MySearch
},
data () {
return {
searchTitle: '',
listData: [],
loading: true,
page: 1
}
},
computed: {
qsListData () {
return this.listData.map(x => {
x.showTitle = getLimitTitle(x.title, 30)
return x
})
}
},
methods: {
async infiniteHandler ($state) {
try {
const data = await api.getQsList(this.page++, this.searchTitle)
const len = data.length
if (len > 0) {
this.listData = this.listData.concat(this.formateData(data))
if (len < 10) {
$state.loaded()
} else {
$state.complete()
}
} else {
$state.complete()
}
} catch (err) {
console.error(err)
}
},
formateData (data = []) {
return data.map(x => {
x.link = formatLink(x.link)
return x
})
},
setData (data = []) {
this.listData.splice(0)
if (data.length > 0) {
this.listData = this.formateData(data)
}
},
async handleSearch (value) {
this.searchTitle = value
this.page = 1
this.fetchData()
},
formatStatus (status) {
switch (status) {
case '1':
return '进行中...'
case '2':
return '已结束'
default:
return '未开始'
}
},
getTime (time) {
return dateFormat(time * 1000, 'YYYY年MM月DD日')
},
async fetchData () {
this.loading = true
try {
const data = await api.getQsList(this.page++, this.searchTitle)
this.setData(data)
} catch (err) {
console.error(err)
}
this.loading = false
}
},
mounted () {
this.fetchData()
}
}



